<template>
<div>
      <form v-on:submit.prevent ="request()">
         <b-field class="centerme">
            <b-input placeholder="Search..."
                class="searchbox"
                type="search"
                icon-pack="fas"
                icon="search"
                v-model="searchText"
               >
            </b-input>
        </b-field>
    </form>
<div class="card">
  <div class="card-content">
    <div class="media">
      <div class="media-left">
        <figure class="image is-128x128">
          <img :src='`${results.image}`' alt="Placeholder image">
        </figure>
      </div>
      <div class="media-content">
        <h8 id="cap">{{ this.obj }}</h8>
        <p>{{ this.genres }}</p>
        <p> {{ this.runtimeStr}} | {{ this.directors }} | {{ this.releaseDate }}</p>
        <p>Ratings</p>
        <p> {{ this.imdb }}</p>
        <p>{{ this.stars }}</p>
      </div>
    </div>
    <br><br>
    <div class="content">
      <p> {{ this.plot }}</p>
    </div>
  </div>
</div>
</div>
</template>

<script>
import axios from 'axios'
export default {
     data() {
        return {
            searchText: '',
            previousText: this.$route.params.message,
            obj:[],
            id:[],
            results:[],
            genres:[],
            directors:[],
            runtimeStr:[],
            releaseDate:[],
            plot:[],
            imdb:[],
            stars:[]
        };
    },
    async created() {
            try {
             const res = await axios.get(`https://imdb-api.com/en/API/Search/k_4sulhx8f/${this.previousText}`)
             this.results = res.data.results[0]
             this.id = this.results.id
             const resp = await axios.get(`https://imdb-api.com/en/API/Title/k_4sulhx8f/${this.id}/FullActor,Posters`)
             console.log(resp)
             this.genres = resp.data.genres
             this.runtimeStr = resp.data.runtimeStr
             this.releaseDate = resp.data.releaseDate
             this.directors = resp.data.directors
             this.plot = resp.data.plot
             this.imdb = resp.data.imDbRating
             this.stars = resp.data.stars
             this.obj = res.data.expression
            }catch(err) {
              console.log(err)
            }
     },
     methods: {
async request () {
       try {
             const res = await axios.get(`https://imdb-api.com/en/API/Search/k_4sulhx8f/${this.searchText}`)
             this.results = res.data.results[0]
             this.id = this.results.id
             const resp = await axios.get(`https://imdb-api.com/en/API/Title/k_4sulhx8f/${this.id}/FullActor,Posters`)
             console.log(resp)
             this.genres = resp.data.genres
             this.runtimeStr = resp.data.runtimeStr
             this.releaseDate = resp.data.releaseDate
             this.directors = resp.data.directors
             this.plot = resp.data.plot
             this.imdb = resp.data.imDbRating
             this.stars = resp.data.stars
             this.obj = res.data.expression
            }catch(err) {
              console.log(err)
            }
     }
     }
}
</script>

<style>
#cap {
      text-transform: capitalize;
      padding-top: 20px;
      font-size: 25px;
}
#background {
    background-color: rgb(10, 188, 233) !important;
}
.searchbox {
  width: 80vw;
  margin-left: 10vw;
  margin-right: 10vw;
}
</style>